.contact-form {
    background-color: #3D4451;
    border: 1px solid #ccc;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 80%;
    height: 90%;
    margin: 0 auto;
    padding: 10px;
  }
  
  .email-input,
  .number-input {
    width: 100%;
    margin-bottom: 20px;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 4px;
  }
  
  .message-input {
    width: 100%;
    margin-bottom: 20px;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 4px;
    height: 200px;
  }
  
  .submit-button {
    width: 50%;
    padding: 10px;
    border: none;
    border-radius: 5px;
    background-color: #b48608;
    color: rgb(10, 10, 10);
    font-size: 16px;
    cursor: pointer;
    font-weight: 700;
  }
  